<template>
  <body>
    <div class="container">
      <div class="background">
        <div class="shape"></div>
        <div class="shape"></div>
      </div>
      <div class="alert alert-warning" role="alert" v-if="message">
        {{ message }}
      </div>
      <form method="post" @submit.prevent="signin">
        <h3>Login Here</h3>
        <validation-provider rules="required" v-slot="{ errors }">
          <label for="username">Username/Email</label>
          <input
            type="text"
            v-model="user_data.username"
            placeholder="Username/Email"
            id="username"
          />
          <span class="required_field_false">{{ errors[0] }}</span>
        </validation-provider>

        <validation-provider rules="required" v-slot="{ errors }">
          <label for="password">Password</label>
          <input
            type="password"
            v-model="user_data.password"
            placeholder="Password"
            id="password"
          />
          <span class="required_field_false">{{ errors[0] }}</span>
        </validation-provider>

        <button>Log In</button>
        <div class="mt-2">
          <div class="offset-1" to="#">Want to SignUp Contact Admin</div>
        </div>
      </form>
    </div>
  </body>
</template>

<script>
export default {
  data() {
    return {
      user_data: {
        username: "",
        password: "",
      },
      message: "",
    };
  },
  methods: {
    signin: function () {
      this.$http
        .post(this.$store.getters.url + "/api-login", this.user_data)
        .then(function (data) {
          this.$store.dispatch("set_token", data.body.token);
          this.$store.dispatch("troggle_on_auth").then((d) => {
            this.$http
              .get(this.$store.getters.url + "/user-profile")
              .then((data) => {
                if (data.body.is_staff) {
                  this.$store.dispatch("troggle_on_admin").then((d) => {
                    this.$router.go();
                  });
                }
              });
          });

          this.message = "";
        })
        .catch((e) => {
          this.message = "Please check User name or Password";
        });
    },
  },
};
</script>

<style scoped>
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #080710;
}

.background {
  width: 430px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}
.background .shape {
  height: 200px;
  width: 200px;
  position: absolute;
  border-radius: 50%;
}
.shape:first-child {
  background: linear-gradient(#1845ad, #23a2f6);
  left: -80px;
  top: -80px;
}
.shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  right: -30px;
  bottom: -80px;
}
form {
  height: 520px;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.13);
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 50px 35px;
}
form * {
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}
form h3 {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
}

label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}
input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 300;
}
::placeholder {
  color: #e5e5e5;
}
button {
  margin-top: 50px;
  width: 100%;
  background-color: #ffffff;
  color: #080710;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
}
.required_field_false {
  color: crimson;
}
</style>



